.auth {
  margin: 3rem auto;
  width: 95%;
  max-width: 25rem;
  border-radius: 6px;
  background-color: rgb(104, 154, 154);
  box-shadow: 0 1px 4px rgb(104, 154, 154);
  padding: 1rem;
  text-align: center;
  color:white
}

.auth h1 {
  text-align: center;
  color: white;
}

.control {
  margin-bottom: 0.5rem;
}

.control label {
  display: block;
  color: rgb(199, 228, 228);
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.control input {
  font: inherit;
  background-color:white;
  color:black;
  border-radius: 4px;
  border: 1px solid white;
  width: 100%;
  text-align: left;
  padding: 0.25rem;
}

.actions {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.actions button {
  cursor: pointer;
  font: inherit;
  color: white;
  background-color: rgb(85, 83, 83);
  border: 1px solid #777;
  border-radius: 4px;
  padding: 0.5rem 2.5rem;
}

.actions button:hover {
  background-color:  rgb(57, 53, 53);
  border-color:rgb(57, 53, 53);
}

.actions .toggle {
  margin-top: 1rem;
  background-color: transparent;
  color: white;
  border: none;
  padding: 0.15rem 1.5rem;
}

.actions .toggle:hover {
  background-color: transparent;
 
  text-decoration: underline;
}